* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.body {
    letter-spacing: 0.675pt;
    position: relative;
    background-color: #0e0e0e;
    min-height: 100vh;
    max-width: 100vw;
    align-items: center;
    color: #ffffff;
    display: flex;
    flex-direction: column;

    &__image {
        width: 100%;
        max-height: 65vh;
        // background-image: url("https://inaturalist-open-data.s3.amazonaws.com/photos/2008/large.jpg");
        // background-size: cover;
        // background-position: center;
        // background-repeat: no-repeat;
        // background-color: red;
    }
}

.link {
    text-align: center;
    margin: 0 auto;
    width: 100%;
    background-color: #4e9f3d;
    padding: 0.625rem 0;
    color: white;
}

.text {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 2vh 5%;
    height: 35vh;
    justify-content: space-between;
    &__scientific,
    &__common,
    &__appeared {
        margin-bottom: 1rem;
        &--add {
            color: #cacaca;
            font-weight: lighter;
        }
    }
    &__scientific {
        &--add {
            font-style: italic;
        }
    }

    &__button {
        position: fixed;
        bottom: 0;
        left: 0;
        background-color: #0e201c;
        width: 100%;
        text-align: center;
        padding: 1.5rem 0;
        text-align: center;
        font-size: 1.6rem;
        color: #c0ad62;
        border: 1px solid #fff;
        font-weight: 700;
        cursor: pointer;
    }
}
